header {
    position: relative;
    height: 50px;
    background-color: red; }

nav {
    position: fixed;
    width: 100%;
    height: 50px;
    background: #212121;
    z-index: 2; }

a {
    text-decoration: none; }

.menu {
    display: flex;
    justify-content: space-between;
    padding: 15px 70px;
    margin: 0;
    list-style-type: none;

    &_link {
        font-weight: 300;
        font-size: 15px;
        align-items: center;
        color: #FFFFFF; }

    &_item {
        border-right: 2px black dotted;
        padding-right: 30px; } }

.subheader {
    top: 50px;
    position: absolute;
    width: 100%;
    height: 84px;
    background: rgba(0, 0, 0, 0.6);

    &_img {
        display: block;
        max-width: 100%;
        margin-top: 17px; }

    &_underlogo {
        font-weight: 200;
        font-size: 13px;
        align-items: center;
        color: #FFFFFF; }

    &_call {
        margin-top: 22px;
        font-weight: 300;
        font-size: 12px;
        line-height: 14px;
        align-items: center;
        text-align: right;
        color: #FFFFFF; }

    &_phone {
        font-size: 22px;
        text-align: right;
        text-transform: uppercase;
        & a {
            text-align: right;
            color: #FFFFFF; } }

    &_btn {
        display: block;
        margin-top: 20px;
        width: 172px;
        height: 46px;
        background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
        box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
        border-radius: 4px;
        font-weight: normal;
        font-size: 14px;
        line-height: 46px;
        align-items: center;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
        text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
        @include btn_hover; } }

.general {
    min-height: 700px;
    background: url('../img/bg/uber_bg.png') center center/cover no-repeat;
    padding: 145px 0 0px 0;

    &_header {
        font-weight: 900;
        font-size: 60px;
        line-height: 24px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF; }

    &_subheader {
        margin-top: 22px;
        font-weight: 700;
        font-size: 36px;
        line-height: 48px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF; }

    &_text {
        margin-top: 16px;
        font-weight: normal;
        font-size: 18px;
        line-height: 24px;
        text-align: center;
        color: #FFFFFF; }

    &_btn {
        display: block;
        text-align: center;
        margin: 116px auto 0;
        width: 259px;
        height: 63px;
        background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
        box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
        border-radius: 4px;
        font-weight: 300;
        font-size: 18px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
        text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
        @include btn_hover; } }

.couses {
    padding-top: 20px;
    min-height: 800px;

    &_btn {
        display: block;
        margin: 15px auto 0 auto;
        width: 115px;
        height: 27px;
        background: #000000;
        width: 113px;
        height: 24px;
        font-weight: 400;
        font-size: 11px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF; }

    &_header {
        margin-top: 32px;
        font-weight: 700;
        font-size: 38px;
        align-items: center;
        text-align: center;
        text-transform: uppercase;
        color: #222222; }

    &_round {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 116px;
        height: 116px;
        background: #1EACC7;
        margin-right: 25px;
        border-radius: 100%; }

    &_text {
        font-weight: normal;
        font-size: 17px;
        line-height: 21px;
        align-items: center;
        color: #1EACC7; }

    &_subtext {
        font-weight: 300;
        font-size: 15px;
        line-height: 21px;
        align-items: center;
        color: #202020; }

    &_block {
        display: flex;
        justify-content: center;
        align-items: top;
        padding-top: 16px; }

    &_descr {
        width: 372px; } }

.app {
    position: relative;
    min-height: 850px;
    padding: 48px 0 80px 0;
    background: url('../img/bg/mobile_bg.png') center center/cover no-repeat;
    .row {
        margin-top: 50px; }

    &_btn {
        display: block;
        margin: auto auto;
        width: 115px;
        height: 27px;
        background: #000000;
        width: 113px;
        height: 24px;
        font-weight: 400;
        font-size: 11px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
        @include btn_hover; }

    &_header {
        margin-top: 32px;
        font-weight: 700;
        font-size: 38px;
        align-items: center;
        text-align: center;
        text-transform: uppercase;
        color: #222222; }

    &_discr {
        font-weight: normal;
        font-size: 17px;
        align-items: center;
        text-align: center;
        color: #222222; }

    &_item {
        margin: 0 auto;
        padding-top: 92px;
        position: relative;
        display: flex;
        justify-content: center;
        // align-items: center
        width: 320px;
        height: 280px;
        margin-bottom: 19px;
        &_1 {
            background: url('../img/карточка 1.png') center center/cover no-repeat; }
        &_2 {
            background: url('../img/карточка 2.png') center center/cover no-repeat; }
        &_3 {
            background: url('../img/карточка 3.png') center center/cover no-repeat; }
        &_4 {
            background: url('../img/карточка 4.png') center center/cover no-repeat; }
        &_5 {
            background: url('../img/карточка 5.png') center center/cover no-repeat; }
        &_6 {
            background: url('../img/карточка 6.png') center center/cover no-repeat; } }
    &_subtitle {
        display: block;
        font-weight: normal;
        word-wrap: break-word;
        font-size: 24px;
        line-height: 31px;
        align-items: center;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF; }
    &_plus {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        bottom: 35px;
        width: 29px;
        height: 29px;
        background: #000000;
        margin: 0 auto;
        border-radius: 100%;
        padding: 6px;
        cursor: pointer;
        &::before {
            content: '';
            display: block;
            position: absolute;
            width: 14px;
            height: 2px;
            background-color: $main_color;
            transition: 0.5s all; }
        &::after {
            position: absolute;
            content: '';
            display: block;
            width: 2px;
            height: 14px;
            background-color: $main_color;
            transition: 0.5s all; }
        &:hover {
            &::before, &::after {
                background-color: $second_color; } } } }
.choice {
    position: relative;
    min-height: 895px;
    padding: 57px 0 138px 0;
    background: url('../img/bg/mobile_bg.png') center (center / cover) no-repeat;
    &_btn {
        display: block;
        margin: auto auto;
        width: 115px;
        height: 27px;
        background: #FFFFFF;
        width: 113px;
        height: 24px;
        font-weight: 400;
        font-size: 11px;
        text-align: center;
        text-transform: uppercase;
        color: #000000;
        z-index: 3; }
    &_img {
        position: relative;
        display: block;
        margin: 56px auto 0 auto;
        z-index: 1; }
    &_descr {
        text-align: center;
        color: #222222;
        font-size: 17px;
        font-weight: 300;
        line-height: 24px; }
    .black {
        position: absolute;
        top: 0;
        left: 0;
        height: 499px;
        width: 100%;
        background-color: #070716;
        z-index: 1; } }

.label {
    display: block;
    position: relative;
    margin: 0 auto;
    height: 24px;
    width: 112px;
    padding: 5px 12px;
    background-color: #000000;
    color: #ffffff;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center;
    z-index: 2;
    &_white {
        background-color: #fff;
        color: #000; } }

.title {
    position: relative;
    margin-bottom: 0;
    margin-top: 38px;
    color: #222222;
    font-size: 38px;
    line-height: 38px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    z-index: 2;
    &_white {
        color: #fff; } }

.subtitle {
    position: relative;
    margin-top: 19px;
    color: #222222;
    font-size: 17px;
    font-weight: 400;
    text-align: center;
    z-index: 2;
    &_white {
        color: #fff; } }

.require {
    min-height: 917px;
    padding: 66px 0 87px 0;
    &_block {
        margin: 33px 0;
        &_nmb {
            margin-bottom: 0px; } }

    &_title {
        font-weight: 400;
        font-size: 22px;
        line-height: 24px;
        align-items: center;
        color: #1CA8C3; }

    &_list {
        list-style-type: none;
        padding-left: 50px;
        margin-top: 22px;
        li {
            font-weight: 300;
            font-size: 17px;
            color: #222222;
            margin-bottom: 10px;
            &::before {
                content: '';
                display: inline-block;
                vertical-align: middle;
                width: 21px;
                height: 20px;
                background: url('../img/icons/check_blue 8.png') center (center/cover) no-repeat;
                transform: translate(-5px, -2px); } } }
    .warning {
        .require_title {
            color: $second_color; }
        .require_list {
            li {
                &:before {
                    background: url('../img/icons/check_red.png') center (center/cover) no-repeat; } } } } }

.world {
    min-height: 720px;
    background: url('../img/bg/world_bg.png') center (center/cover) no-repeat;
    padding: 48px 0 50px 0;
    &_btn {
        display: block;
        margin: 480px 0 0 0;

        font-weight: 400;
        font-size: 15px;
        line-height: 68px;
        align-items: center;
        text-align: center;
        text-transform: uppercase;
        color: #1FBAD6;
        text-transform: none; } }

.footer {
    min-height: 156px;
    margin: 16px 0 40px 0;
    &_line {
        margin: 0 auto;
        width: 301px;
        height: 7px;
        background: #1FBAD6; }

    &_wrapper {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding-top: 32px; }
    &_wrapper > div {
        width: 33%;
        &:nth-child(3) {
            display: flex;
            justify-content: flex-end; } }
    &_social {
        width: 160px;
        display: flex;
        justify-content: space-between;
        &_item {
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 4px;
            width: 32px;
            height: 32px;
            background-color: #070716;
            border-radius: 100%;
            i {
                color: #fff; } } }
    &_links {
        width: 390px;
        margin: 0 auto;
        &_main {
            display: flex;
            justify-content: space-between;
            padding: 0 10px;
            a {
                color: #070716;
                font-size: 15px;
                font-weight: 300;
                text-transform: uppercase; } }
        &_sub {
            display: flex;
            justify-content: space-between;
            margin-top: 10px;
            a {
                color: #999999;
                font-size: 11px;
                font-weight: 300;
                text-transform: uppercase; } }
        &_lang {
            display: block;
            margin: 0 auto;
            color: #1fbad6;
            font-size: 11px;
            font-weight: 400;
            margin-top: 19px;
            text-transform: uppercase;
            text-align: center; } }

    &_mobile {} }
